import React, { Component } from "react";
import {
    View,
    Text,
    StyleSheet,
    Button,
    ScrollView
} from "react-native";


function Inbox({route ,navigation }) {
    return (
      <View style={{ flex: 1/*, alignItems: 'center', justifyContent: 'center' */}}>
        <ScrollView scrollEventThrottle={16}>
            <View style={{flex:1,paddingTop:20}}>
        <Text style={{fontSize:24,fontWeight:'700',paddingHorizontal:20}}>Comment puis-je vous aider ?</Text>
        <View style={{height:130, marginTop:20}}>
            <ScrollView horizontal={true}>
                <View style={{marginLeft:20}}>
                    <View style={{flex:2}}>
                    <Button title="Cartes électroniques" onPress={() => navigation.navigate("Explorer les cartes électroniques")}  style={{backgroundColor: 'red'}}/>
                    </View>
                </View>
                <View style={{height:130, width:130, marginLeft:20,borderWidth:0.5}}>
                    <View style={{flex:2}}>
                    <Button title="Composants" onPress={() => navigation.navigate("Explorer les composants électroniques")}  style={{backgroundColor: 'red'}}/>
                    </View>
                </View>
                <View style={{height:130, width:130, marginLeft:20,borderWidth:0.5}}>
                    <View style={{flex:2}}>
                    </View>
                </View>
                <View style={{height:130, width:130, marginLeft:20,borderWidth:0.5}}>
                    <View style={{flex:2}}>
                    </View>
                </View>
            </ScrollView>
        </View>
        </View>
            </ScrollView>        
            
            <Button title="Go back" onPress={() => navigation.navigate('exploreComposants')} />
      </View>
    );
  }
export default Inbox;


const styles = StyleSheet.create({
    container: {
        flex: 1,
        alignItems: 'center',
        justifyContent: 'center'
    },
    button: {
        backgroundColor: 'white',
        shadowColor: "#000",
        shadowOffset: {
	    width: 0,
	    height: 6,
        },
        shadowOpacity: 0.39,
        shadowRadius: 8.30,
        elevation: 13,
    }
});

