import { StatusBar } from "expo-status-bar";
import React, { Component } from "react";
import {
    View,
    Text,
    StyleSheet,
    ImageBackground,
    ScrollView,
    SectionList
} from "react-native";
import {Image,Card,ListItem,Avatar,Divider} from 'react-native-elements';
let Image_Http_URL ={ uri: 'https://reactnativecode.com/wp-content/uploads/2017/05/react_thumb_install.png'};
const list = require('./cartes.json');

 

function Trips({ route,navigation }) {
    let data =route.params
    const obj = list[data.id]
        return (
            <ScrollView>
            <View>
                <ImageBackground source={{ uri: obj.image}} style={styles.image} imageStyle={{}}>
                <Text style={styles.Tagline}>{obj.nom}</Text>
                </ImageBackground>
            </View>
            <View style={styles.container}>

        <ListItem roundAvatar style={{paddingTop:5,paddingBottom:5}}title="Clock" subtitle={obj.clock+" "} />
        <ListItem roundAvatar style={{paddingTop:5}}title="Nombre de GPIO" subtitle={obj.nb_pins+" "} />
        <ListItem roundAvatar title="Nombre de pins Analogiques" subtitle={obj.nb_pins_analog+" "} />
        <ListItem roundAvatar style={{paddingBottom:5}}title="Nombre de pins Numériques" subtitle={obj.nb_pins_num+" "} />
        <ListItem roundAvatar style={{paddingTop:5}}title="Mémoire Flash" subtitle={obj.memoire_flash+" "} />
        <ListItem roundAvatar style={{paddingBottom:5}}title="Mémoire Vive" subtitle={obj.memoire_vive+" "} />
        <ListItem roundAvatar style={{paddingTop:5,paddingBottom:5}}title="Connectivité" subtitle={obj.connectivite+" "} />



        </View>
        </ScrollView>
         )
         }
export default Trips;

const styles = StyleSheet.create({
    image: {
        height: 200,
        justifyContent: 'flex-end',
        backgroundColor: 'rgba(0,0,0,0.4)',
          },
    Tagline: {
        color: 'black',
        fontSize: 16,
        fontWeight: 'bold',
        paddingHorizontal: 14,
        marginBottom: 25
    },
    sectionHeader: {
        paddingTop: 2,
        paddingLeft: 10,
        paddingRight: 10,
        paddingBottom: 2,
        fontSize: 14,
        fontWeight: 'bold',
        backgroundColor: '#95a5a6',
      },
      item: {
        padding: 10,
        fontSize: 18,
        height: 44,
        backgroundColor: 'rgba(247,247,247,1.0)',

      },
});