import React, { Component } from 'react';
import { View, Text, FlatList, ActivityIndicator,StyleSheet,TextInput,ScrollView,SafeAreaView,Button} from 'react-native';
import { ListItem, SearchBar, Avatar } from 'react-native-elements';
const list = require('./cartes.json');
import { withNavigation } from 'react-navigation';
import { NavigationContainer } from '@react-navigation/native';
import { createStackNavigator } from '@react-navigation/stack';
import { createBottomTabNavigator } from '@react-navigation/bottom-tabs';
import { useNavigation } from '@react-navigation/native';



class App extends Component {
  state = {
    search: '',
  };

  updateSearch = (search) => {
    this.setState({ search });
  };
  filterList(list) {
    return list.filter((listItem) =>
        listItem.nom.toLowerCase() .includes(this.state.search.toLowerCase()),
    );
  }
  render() {
    const { search } = this.state;

    return (
              <ScrollView>

      <View style={styles.container}>
              <SearchBar
        placeholder="Type Here..."
        onChangeText={this.updateSearch}
        value={search}
      />
      </View>

        {this.filterList(list).map((listItem, index) => (

        <ListItem bottomDivider onPress={() =>   this.props.navigation.navigate('Details' , { id:listItem.id})}>        
        <ListItem.Content  key={index}>
        <Avatar source={{uri:listItem.image}} />
        <ListItem.Title>{listItem.nom}</ListItem.Title>
        <ListItem.Subtitle>{listItem.nom}</ListItem.Subtitle>
        </ListItem.Content>
        </ListItem>
        ))}

      <View>

    </View>
    </ScrollView>
    );
  }
}

  

  
const styles = StyleSheet.create({

  searchBar: {
    fontSize: 24,
    margin: 10,
    width: '90%',
    height: 50,
    backgroundColor: 'white',
  },
});


export default withNavigation(App);