import * as React from 'react';
import { Button, Text, View } from 'react-native';
import { NavigationContainer } from '@react-navigation/native';
import { createStackNavigator } from '@react-navigation/stack';
import { createBottomTabNavigator } from '@react-navigation/bottom-tabs';
import Saved from "./screens/Saved.js"
import Trips from "./screens/Trips.js"
import Inbox from "./screens/Inbox.js"
import exploreComposants from './screens/explore-composants.js';
function DetailsScreen() {
  return (
    <View style={{ flex: 1, justifyContent: 'center', alignItems: 'center' }}>
      <Text>Details!</Text>
    </View>
  );
}

function HomeScreen({ navigation }) {
  return (
    <View style={{ flex: 1, justifyContent: 'center', alignItems: 'center' }}>
      <Text>Home screen</Text>
      <Button
        title="Go to Details"
        onPress={() => navigation.navigate('Details')}
      />
    </View>
  );
}

function SettingsScreen({ navigation }) {
  return (
    <View style={{ flex: 1, justifyContent: 'center', alignItems: 'center' }}>
      <Text>Settings screen</Text>
      <Button
        title="Explorer les cartes électroniques"
        onPress={() => navigation.navigate("Explorer les cartes électroniques")}
      />
      <Button
        title="Explorer les composants électroniques"
        onPress={() => navigation.navigate("Explorer les composants électroniques")}
      />
    </View>
  );
}

const HomeStack = createStackNavigator();

function HomeStackScreen() {
  return (
    <HomeStack.Navigator>
      <HomeStack.Screen name="Home" component={Inbox} />
      <HomeStack.Screen name="Details" component={DetailsScreen} />
    </HomeStack.Navigator>
  );
}

const SettingsStack = createStackNavigator();

function SettingsStackScreen() {
  return (
    <SettingsStack.Navigator>
      <SettingsStack.Screen name="Rubriques" component={SettingsScreen} />
      <SettingsStack.Screen name="Explorer les cartes électroniques" component={Saved} />
      <SettingsStack.Screen name="Explorer les composants électroniques" component={exploreComposants} />
      <SettingsStack.Screen name="Details" component={Trips} />
    </SettingsStack.Navigator>
  );
}

const Tab = createBottomTabNavigator();

export default function App() {
  return (
    <NavigationContainer>
      <Tab.Navigator>
        <Tab.Screen name="Acceuil" component={HomeStackScreen} />
        <Tab.Screen name="Electronique" component={SettingsStackScreen} />
      </Tab.Navigator>
    </NavigationContainer>
  );
}